import common from'../../util/common.js'
import qs from"../../components/qs.js"
export default {
    data() {
        return {
			tabIndex: 0, //选中标签栏的序列
			tabTList:[],//
			activeE:"",//1有值0 为空值
			tabList:[],
		}
    },
	onLoad() {
		 var answerId = uni.getStorageSync("answerIdS");
		if (answerId == "" || answerId == undefined || answerId == null || answerId == NaN || answerId == 'NaN') {
			uni.showToast({
				title: '请联系管理员',
				icon: 'none',
				duration: 2000
			})
			return	
		}else{
			var data =  {
				answer_id: answerId,
				is_right:'0'
			}
			var answerData = qs.stringify(data);
			uni.request({
				url: common.port.mMistakes,
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				method: "POST",
				dataType: "json",
				data:answerData,
				success: (res) => {
					console.log(res.data);
					if (res.data.code == 500) {
						this.activeE = 1
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 2000
						})
						return
					} else {
						this.tabList = res.data.questions;
						console.log(this.tabList)
						this.tabTList = res.data.statistics;
						if(res.data.statistics.total > 0){
							this.activeE = 1
						}
						
					}
				}
			})
		}
		
	},
    methods: {
		//点击
        toggleTab(index){
        	console.log(index)
        	this.tabIndex = index
			if(index == 0){
				var data =  {
					answer_id: uni.getStorageSync("answerIdS"),
					is_right:'0'
				}
			}else{
				var data ={
					answer_id: uni.getStorageSync("answerIdS"),
					type:index,
					is_right:'0'
				}
			}
			var answerData = qs.stringify(data);
			uni.request({
				url: common.port.mMistakes,
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				method: "POST",
				dataType: "json",
				data:answerData,
				success: (res) => {
					console.log(res.data);
					if (res.data.code == 500) {
						this.activeE = 1
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 2000
						})
						return
					} else {
						this.tabList = res.data.questions;
						console.log(this.tabList)
						this.tabTList = res.data.statistics;
						if(res.data.statistics.total > 0){
							this.activeE = 1
						}
						
					}
				}
			})
        },
    }
}
